var _self;
export default {
	data() {
		return {
			addressData: {
				address_id : 0,
				consignee: '',
				mobile: '',
				address_name: '在地图选择',
				address: '',
				use: false
			},
			manageType : ''  ,//add或edit
		}
	},
	onLoad(option){
		_self = this;
		let title = '新增收货地址';
		if(option.type==='edit'){
			title = '编辑收货地址'
			
			this.addressData = JSON.parse(option.data)
			//console.log('取得的地址信息“',this.addressData);
		}
		this.manageType = option.type;
		uni.setNavigationBarTitle({
			title
		})
	},
	methods: {
		switchChange(e){
			_self.addressData.use = e.target.value;
			//console.log(e.target.value);
		},
		
		//地图选择地址
		chooseLocation(){
			uni.chooseLocation({
				success: (data)=> {
					this.addressData.address_name = data.address;
					//console.log('地图返回',data);
					//this.addressData.address = data.name;
				}
			})
		},
		
		//提交
		async confirm(){
			let data = this.addressData;
			if(!data.consignee){
				this.$api.msg('请填写收货人姓名');
				return;
			}
			if(!/(^1[3|4|5|7|8][0-9]{9}$)/.test(data.mobile)){
				this.$api.msg('请输入正确的手机号码');
				return;
			}
			if(!data.address_name){
				this.$api.msg('请在地图选择所在位置');
				return;
			}
			if(!data.address){
				this.$api.msg('请填写门牌号信息');
				return;
			}
			//新数据提交服务
			if (_self.manageType === 'edit') {
				let res = await _self.$apiRequest({
					method: 'PUT',
					url:'addresses/'+data.address_id,
					data: data
				});
				//console.log(res);
			} else {
				let res = await _self.$apiRequest({
					method: 'POST',
					url:'addresses',
					data: data
				});
				//console.log(res);
			}
			//this.$api.prePage()获取上一页实例，可直接调用上页所有数据和方法，在App.vue定义
			//_self.$api.msg(data);
			 this.$api.prePage().refreshList(data, this.manageType);
			 this.$api.msg(`地址${this.manageType=='edit' ? '修改': '添加'}成功`);
			 setTimeout(()=>{
			 	uni.navigateBack()
			 }, 800)
		},
		async delA(){
			let delRes = await _self.$apiRequest({
				method:'DELETE',
				url:'addresses/'+_self.addressData.address_id
			});
			//console.log(delRes);
			if (delRes.data.success){
				uni.navigateBack({delta: 1});
			} else {
				_self.$api.msg(delRes.data.message);
			}
		},
		deleteAddress() {
			uni.showModal({
				title:'你确定要删除这个地址吗？',
				success: (res) => {
					 if(res.confirm) {
						_self.delA();
					}
				}
			})
		},
	}
}
